import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";

function PlannerSummary({ points, distance, altitude }) {

    return (
        <Box className="planner-summary-box">
            <Typography variant="h6" align="center">Suma punktw za tras: {points}</Typography>
            <Typography variant="h6" align="center">Odlego trasy: {distance}km</Typography>
            <Typography variant="h6" align="center">Rnica wysokoci: {altitude}m</Typography>
        </Box >
    );
}

export default PlannerSummary;